.comparison {
	margin: 60px 0;
	position: relative;

	@include bp(900px) {
		margin: 40px 0;
	}

	h3 {
		color: $white;
		text-align: center;
		margin-top: 20px;
	}

	&__inner {
		position: relative;
		z-index: 10;
	}

	&__background {
		position: absolute;
	    top: 60px;
	    left: 0;
	    right: 0;
	    bottom: 60px;
	    z-index: 1;

	    @include bp(900px) {
			height: calc(100% - 80px);
			margin: 40px 0;
		}
	}

	&__wrapper {
		display: flex;

		@include bp(900px) {
			flex-wrap: wrap;
		}
	}

	&__single {
		flex: 1;
		padding: 20px 40px;
		box-shadow: 0px 0px 16px 0px rgba(0,0,0,0.50);
		background-color: rgba(0, 0, 0, 0.7);

		@include bp(900px) {
			flex: 0 0 100%;
			margin: 10px 0;
		}

		&:first-child {
			margin-right: 20px;
			cursor: e-resize;

			@include bp(900px) {
				margin-right: 0;
			}
		}

		&:last-child {
			margin-left: 20px;
			cursor: w-resize;

			@include bp(900px) {
				margin-left: 0;
			}
		}

		ul {
			padding-left: 1em;
		}

		li {
			color:$white;
			transform-origin: 0 0;
			margin-bottom: 1em;

			p {
				margin: 0;
				padding: 0;
				font-size: 1.1em;
			}

			&:before {
				color:$white;
			}

			&.flash {
				animation: comparisonate .5s ease-in-out;
			}
		}
	}
}

@keyframes comparisonate {
	0% {opacity: 0; transform: scale(.9);}
	33% {transform: scale(1);}
	50% {transform: scale(1.05);}
	67% {transform: scale(1);}
}
