.blog-torso{
	&__content{
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		width: 100% !important;
		padding: 0 !important;
	}
	&__inner {
		& > h1 { // main page heading
			font-size: 1.4em;
			font-family: $fam-heading;
			text-transform: uppercase;
			font-weight: normal;
			text-align: center;
			position: relative;
			margin: 1em 0;
			color: $color-1;
			padding-bottom: 20px;
			float: left;
			width: 100%;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 50%;
				width: 100px;
				margin-left: -50px;
				height: 2px;
				background-color: $color-2;
			}
		}
	}
	&__sidebar {
		width: 100% !important;

		ul {
			text-align: center;
			padding: 2em $pad;
		}
		li {
			display: inline-block;
			padding: 0;
			margin: 0 .5em;
			border-bottom: 2px solid $color-2;

			&:before{
				display: none;
			}

			a {
				padding: .25em 1.5em;
				display: block;
				width: 100%;
				position: relative;
			}

			@include bp(650px){
				display: block;
				width: 100%;
				border-color: $color-1;
			}

			&.current-cat {
				border: 0;
				a {
					color: $white;
					background-color: $color-1;
					text-transform: uppercase;

					// &:after {
					// 	content: '';
					// 	position: absolute;
					// 	bottom: -5px;
					// 	left: 50%;
					// 	width: 100px;
					// 	margin-left: -50px;
					// 	height: 2px;
					// 	background-color: $color-2;
					// }
				}
			}
		}
		#archive{
			padding: .5em 2em;
			display: block;
			color: $color-2;
		}
		#archive-drawer{
			height: 0;
			overflow: hidden;
			transition: .3s;
			&.open{
				height: auto;
				transition: .3s;
			}
			li{
				border: none;
				a{
					color: $color-1;
					padding: .25em 1em;
					&:hover{
						color: $color-2;
					}
				}
			}
		}
	}
}
.single-torso {
	&__post {
		position: relative;
		width: 50%;
		float: left;
		padding: $pad;
		&:nth-of-type(odd){
			clear: both;
		}
		&.default:nth-of-type(4n+1),
		&.default:nth-of-type(4n+2),
		&.text-right{
			.single-torso__text{
				float: right;
			}
			.single-torso__image{
				right: 10%;
			}
		}
		&.default:nth-of-type(4n+4),
		&.default:nth-of-type(4n+3),
		&.text-left{
			.single-torso__text{
				float: left;
			}
			.single-torso__image{
				left: 10%;
			}
		}
		@include bp(690px){
			width: 100%;
			&.default:nth-of-type(odd),
			&.text-right{
				.single-torso__text{
					float: right;
				}
				.single-torso__image{
					right: 10%;
					left: $pad;
				}
			}
			&.default:nth-of-type(even),
			&.text-left{
				.single-torso__text{
					float: left;
				}
				.single-torso__image{
					left: 10%;
					right: $pad;
				}
			}
		}
		&:hover {
			.single-torso__text {
				background: rgba(0, 55, 115, 0.8);
			}
		}
	}
	&__text{
		width: 45%;
		background: rgba(0, 0, 0, 0.7);
		border: 1px solid $white;
		padding: $pad;
		margin: 1em 0;
		position: relative;
		color: $white;
		h1{
			font-size: emCalc(16pt);
			padding-bottom: .5em;
			color: $white;
			text-align: left;
		}
		@include bp($tablet){
			width: 60%;
		}
	}
	&__image{
		position: absolute;
		top: $pad;
		right: $pad;
		bottom: $pad;
		left: $pad;
		background: $color-2;
		div{
			height: 100%;
		}
	}
	&_content{
		padding: .75em 0;
	}
}

.post-single {

	p {
		margin: 0 0 1.5em 0;
		padding: 0;
		text-align: justify;
		font-size: 1.4em;
		line-height: 1.6;
	}

	&__container {
		position: relative;
	}

	// &__content {
	// 	padding: 40px 0;
	// 	width: 70%;
	// 	float: left;
	// 	@include bp(900px){
	// 		width: 100%;
	// 	}
	// }

	&__social {
		ul{
			margin: 10px 0 0;
			padding: 0;
			list-style: none;
		}

		li{
			display: inline-block;
			margin-right: 5px;
			padding: 0;

			&:before {
				content: '';
			}
		}

		a{
			display: block;
			width: 30px;
			height: 30px;
			cursor: pointer;

			&:focus{
				outline: none;
			}

			&:hover{
				svg{
					fill: $color-1;
				}
			}
		}

		svg{
			width: 100%;
			height: 100%;
			fill: $color-2;
			transition: 300ms;
		}
	}

	&__image {
		height: 30vh;
	}

	&__title {
		padding: 10px 0 5px;

		hgroup {
			margin-top: 70px;
		}

		h1 {
			font-size: 2.3em;
			color: $color-1;
			margin-top: 0;
			// padding-right: 20px;
		}

		h2 {
			color: $color-2;
			text-transform: uppercase;
			font-weight: 300;
			margin-top: 0;
		}
	}

	&__date {
		padding: 5px 0 10px;

		h5 {
			color: $color-2;
			font-size: 1.14em;
		}
	}

	&__text {
		padding: 40px 0;
		font-family: $fam-body;

		a {
			color: $color-2;

			&:hover {
				color: $color-1;
			}
		}
	}

