.post {
	&.main-content.has-aside {
		grid-gap: $gap--huge;
	}
	&__related {
		margin-top: 80px;

		h3 {
			text-align: left;
			&:after {
				left: 0;
				margin-left: 0
			}
		}
	}

}

a.more {
	display: block;
	line-height: 1.4em;
	width: 100%;
	border-radius: 4px;
	border: 1px solid $color-1;
	padding: 15px;
	// text-align: center;
	color: $white;
	background-color: $color-1;
	text-transform: uppercase;
	transition: all 500ms;
	&:hover {
		background-color: $white;
		color: $color-1;
	}
}


.pagination-block{
	clear: both;
	text-align: center;
	width: 100%;
	padding: 2em $pad;
}
