.post-category {
  .content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: $gap--big;
    // align-content: space-between;

    @include bp($s-mobile) {
      grid-template-columns: auto;
    }
  }
}

.img-bg {
  background-repeat: no-repeat;
  background-position: center center;
  &.cover {
    background-size: cover;
  }
  &.contain {
    background-size: contain;
  }
}

.card {
  position: relative;
  color: $color-1;
  display: block;
  h1 {
    margin: 0;
    text-transform: uppercase;
    font-size: 1.7em;
    font-weight: 500;
    line-height: 1.2em;
  }
  h2 {
    // margin: 0;
    margin-top: .5em;
    margin-bottom: 0;
    font-size: 1.3em;
    color: $color-2;
    font-weight: 300;
    padding-bottom: 0;
    line-height: 1.4em;
  }

  hgroup {
    margin: 2em 0 1.5em 0;
  }

  article {
    margin-bottom: 50px;
  }

  &:hover {
    .more {
      background-color: $color-2;
      color: $color-4;
    }

  }

  &__image {
    &__container {
      position: relative;
      border-radius: 4px;
      overflow: hidden;
      &:after {
        position: absolute;
        content: '';
        border-radius: 4px;
        border: 1px solid #fff;
        top: 15px;
        bottom: 15px;
        left: 15px;
        right: 15px;
      }
      &__color-blue {
        &:after {
          border-color: $color-1;
        }
      }
      &__color-white {
        &:after {
          border-color: #fff;
        }
      }
      &__color-orange {
        &:after {
          border-color: $color-2;
        }
      }
    }
    &.img-bg {
      width: 100%;
      height: 0;
      padding: 25%;
    }
  }

  &__label {
    position: absolute;
    top: 15px;
    left: 0;
    background-color: #fff;
    border-radius: 0 0 4px 0;
    padding: 8px 15px;
    padding-left: 20px;
    color: $color-1;
    text-transform: uppercase;
    font-weight: 500;
    z-index:10;
    &__color-blue {
      color: #fff;
      background-color: $color-1;
    }
    &__color-white {
      color: $color-1;
      background-color: #fff;
    }
    &__color-orange {
      color: #fff;
      background-color: $color-2;
    }
  }

  &__content {
    font-weight: 300;
    font-size: 1.2em;
  }

  .more {
    position: absolute;
    bottom: 0;
    width: 100%;
    border-radius: 4px;
    border: 1px solid $color-2;
    padding: 5px;
    text-align: center;
    color: $color-2;
    text-transform: uppercase;
  }

  &__alt {
    margin-bottom: 3em;

    h1 {
      font-weight: 600;
      font-size: 1.4em;
      line-height: 1.4em;
    }

    article {
      margin-bottom: 1em;
    }
    .more {
      position: relative;
    }
  }

  &.current {
    cursor: default;
    .more {
      border-color: $color-1;
      background-color: $color-1;
      color: #fff;
    }
  }
}
