%h-all {
	font-family: $fam-heading;
    font-weight: normal;
    letter-spacing: 0.01em;
    line-height: 1.2;
	margin: 0.4em 0;
}
/*implement:*/%h1, %h2, %h3, %h4, %h5, %h6 { @extend %h-all; }
%h1{
	font-size: 3.2em;
	@include bp($tablet){
		font-size: 2.85em;
	}
	@include bp($s-mobile){
		font-size: 2.43em;
	}
}
%h2 {
	font-size: 2.85em;
	@include bp($tablet){
		font-size: 2.43em;
	}
	@include bp($s-mobile){
		font-size: 1.93em;
	}
}
%h3 {
	font-size: 1.4em;
	font-family: $fam-heading;
	text-transform: uppercase;
	font-weight: normal;
	color: $color-1;
	text-align: center;
	position: relative;
	margin: 0 0 30px;
	padding-bottom: 20px;

	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100px;
		margin-left: -50px;
		height: 2px;
		background-color: $color-2;
	}
}
%h4 {
	font-size: 1.87em;
	color: $color-2;
	font-weight: 300;
	@include bp($s-mobile){
		font-size: 1.4em;
	}
}
%h5 {
	font-size: .9em;
}
%h6 {
	font-size: .7em;
}
/*implement:*/h1{@extend %h1;}h2{@extend %h2;}h3{@extend %h3;}h4{@extend %h4;}h5{@extend %h5;}h6{@extend %h6;}
abbr[title] {
	border-bottom: 1px dotted;
}
b,
strong {
	font-weight: bold;
}
dfn {
	font-style: italic;
}
mark {
	background: #ff0;
	color: #000;
}
small {
	font-size: 80%;
}
sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}
sup {
	top: -0.5em;
}
sub {
	bottom: -0.25em;
}

ul {
	padding: 0;
	margin: 0;
	padding-left: 2em;
	li {
		list-style: none;
		&:before {
			font-family: $fam-icon;
			color: $color-2;
			content: $icon-circle;
			position: absolute;
			font-size: .45em;
			left: -1em;
			top: 1em;
			line-height: 1;
		}
	}
}
li {
	padding: 0 0 0 15px;
	margin: 0;
	position: relative;
}

blockquote {
	position: relative;
	margin: 0;
	font-weight: normal;
	font-style: italic;
	@include bp($tablet){
		margin-left: 15px;
	}
	&:before {
		content: '';
		background-image: url("images/quote.svg");
		background-size: contain;
		background-repeat: no-repeat;
		position: absolute;
		top: 0;
		left: -30px;
		width: 20px;
		height: 20px;
		font-size: 2.85em;
		line-height: .9;
		color: $white;
	}
}
ol li {
  color: #000;
}
